/* Variables
/* ---------------------------------------------------------- */

:root {

    /* Colours */
    --color-primary: #317EFB;
    --color-base: #131313;
    --color-grey: #848e95;
    --color-border: #e2e8ec;
    --color-bg: #f5f7f9;
    --color-success: #80b912;
    --color-error: #f05230;

    /* Fonts */
    --font-sans-serif: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    --font-serif: Georgia, serif;
    --font-mono: Menlo, Courier, monospace, monospace;

    /* Sizes */
    --height: 4rem;
    --margin: 2rem;
    --radius: 0.5rem;

    --content-margin: calc(4vmin + 1em);
}
